<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getGoods"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="goods"
      highlight-current-row
      v-loading="listLoading"
      @selection-change="selsChange"
      style="width: 100%;"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="gname" label="" width="200" sortable></el-table-column>
      <!-- <el-table-column prop="imageurl" label="" min-width="200" sortable></el-table-column> -->
      <el-table-column prop="grade" label="" width="180" sortable></el-table-column>
      <el-table-column prop="num" label="" width="180" sortable></el-table-column>
      <el-table-column label="" min-width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="20"
        :total="total"
        style="float:right;"
      ></el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="gname">
          <el-input v-model="editForm.gname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="imageurl">
          <el-input type="textarea" v-model="editForm.imageurl"></el-input>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-input-number v-model="editForm.grade" :min="1" :max="10000000"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="num">
          <el-input-number v-model="editForm.num" :min="0" :max="1000000"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="gname">
          <el-input v-model="addForm.gname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="imageurl">
          <el-input type="textarea" v-model="addForm.imageurl"></el-input>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-input-number v-model="addForm.grade" :min="1" :max="10000000"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="num">
          <el-input-number v-model="addForm.num" :min="1" :max="1000000"></el-input-number>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import { selGoods, addGoods, editGoods, deleteGoods } from "../../api/api";

export default {
  data() {
    return {
      filters: {
        name: ""
      },
      goods: [
        {
          gname: "",
          imageurl: "",
          grade: "",
          num: ""
        }
      ],
      total: 0,
      currPage: 1,
      pageSize: 20,
      listLoading: false,
      sels: [], //

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        gname: { required: true, message: "", trigger: "blur" },
        imageurl: {
          required: true,
          message: "",
          trigger: "blur"
        },
        grade: { required: true, message: "" },
        num: { required: true, message: "" }
      },
      //
      editForm: {
        gname: "",
        imageurl: "",
        grade: "",
        num: ""
      },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        gname: { required: true, message: "", trigger: "blur" },
        imageurl: {
          required: true,
          message: "",
          trigger: "blur"
        },
        grade: { required: true, message: "" },
        num: { required: true, message: "" }
      },
      //
      addForm: {
        gname: "",
        imageurl: "",
        grade: "",
        num: ""
      }
    };
  },
  methods: {
    //
    formatSex: function(row, column) {
      return row.sex == 1 ? "" : row.sex == 0 ? "" : "";
    },
    handleCurrentChange(val) {
      this.currPage = val;
      this.getGoods();
    },
    //
    getGoods() {
      let para = {
        currPage: this.currPage,
        pageSize: this.pageSize
      };
      // this.listLoading = true;
      //NProgress.start();
      selGoods(para).then(res => {
        console.log("");
        this.total = res.data.data.total;
        this.goods = res.data.data.data;
        this.listLoading = false;
        //NProgress.done();
      });
    },
    //
    handleDel: function(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { gid: row.gid };
          deleteGoods(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getGoods();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        gname: "",
        imageurl: "",
        grade: "",
        num: ""
      };
    },
    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.editForm);
            editGoods(para).then(res => {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getGoods();
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.addForm);
            // para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
            addGoods(para).then(res => {
              console.log("addGoods==>", res);
              this.addLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getGoods();
            });
          });
        }
      });
    },
    selsChange: function(sels) {
      this.sels = sels;
    }
    //
    // batchRemove: function () {
    // 	var ids = this.sels.map(item => item.id).toString();
    // 	this.$confirm('', '', {
    // 		type: 'warning'
    // 	}).then(() => {
    // 		this.listLoading = true;
    // 		//NProgress.start();
    // 		let para = { ids: ids };
    // 		batchRemoveUser(para).then((res) => {
    // 			this.listLoading = false;
    // 			//NProgress.done();
    // 			this.$message({
    // 				message: '',
    // 				type: 'success'
    // 			});
    // 			this.getGoods();
    // 		});
    // 	}).catch(() => {

    // 	});
    // }
  },
  mounted() {
    this.getGoods();
  }
};
</script>

<style scoped>
</style>


