<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.roomintroduce" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getAllRooms"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="rooms" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="roomintroduce" label="" width="150" sortable>
			</el-table-column>
			<!-- <el-table-column prop="roomnumber" label="" width="120" sortable>
			</el-table-column> -->
			<!-- <el-table-column prop="roomimage" label="" min-width="100" sortable>
			</el-table-column> -->
			<el-table-column prop="roomarea" label="(m)" width="150" sortable>
			</el-table-column>
			<el-table-column prop="roomwifi" label="wifi" width="100" sortable>
			</el-table-column>
			<el-table-column prop="roombed" label="(m)" width="150"  sortable>
			</el-table-column>
			<el-table-column prop="roombreakfast" label="" width="100" sortable>
			</el-table-column>
			<el-table-column prop="roomnum" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="roomprice" label="" width="120" sortable>
			</el-table-column>
			<el-table-column label="" min-width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="roomintroduce">
					<el-input v-model="editForm.roomintroduce" auto-complete="off" disabled="disabled"></el-input>
				</el-form-item>
				<!-- <el-form-item label="" prop="roomnumber">
					<el-input-number v-model="editForm.roomnumber" :min="1" :max="200"></el-input-number>
				</el-form-item> -->
				<el-form-item label="" prop="roomimage">
					<el-input type="textarea" v-model="editForm.roomimage"></el-input>
				</el-form-item>
				<el-form-item label="(m)" prop="roomarea">
					<el-input v-model="editForm.roomarea" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="wifi" prop="roomwifi">
					<el-radio-group v-model="editForm.roomwifi">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="(m)" prop="roombed">
					<el-input v-model="editForm.roombed" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roombreakfast">
					<el-radio-group v-model="editForm.roombreakfast">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="roomnum">
					<el-input-number v-model="editForm.roomnum" :min="1" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="roomprice">
					<el-input v-model="editForm.roomprice" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>


		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="roomintroduce">
					<el-input v-model="addForm.roomintroduce" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roomnumber">
					<el-input-number v-model="addForm.roomnumber" :min="1" :max="10000"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="roomimage">
					<el-input type="textarea" v-model="addForm.roomimage"></el-input>
				</el-form-item>
				<el-form-item label="(m)" prop="roomarea">
					<el-input v-model="addForm.roomarea" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="wifi" prop="roomwifi">
					<el-radio-group v-model="addForm.roomwifi">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="(m)" prop="roombed">
					<el-input v-model="addForm.roombed" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="roombreakfast">
					<el-radio-group v-model="addForm.roombreakfast">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="roomnum">
					<el-input-number v-model="addForm.roomnum" :min="1" :max="20"></el-input-number>
				</el-form-item>
				<el-form-item label="" prop="roomprice">
					<el-input v-model="addForm.roomprice" auto-complete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getAllRoom, addRoom, updateRoom, deleteRoom } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				rooms: [],
				total: 0,
				currPage: 1,
				pageSize: 20,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					roomintroduce: { required: true, message: '', trigger: 'blur' },
					// roomnumber: { required: true, message: '', },
					roomimage: { required: true, message: '', trigger: 'blur' },
					roomarea: { required: true, message: '', trigger: 'blur' },
					roomwifi: { required: true, message: 'wifi'},
					roombed: { required: true, message: '' },
					roombreakfast: { required: true, message: ''},
					roomnum: { required: true, message: '' },
					roomprice: { required: true, message: '' },
				},
				//
				editForm: {
					roomintroduce:'',
					// roomnumber: '',
					roomimage:'',
					roomarea:'',
					roomwifi:'',
					roombed:'',
					roombreakfast:'',
					roomnum:'',
					roomprice:''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					roomintroduce: { required: true, message: '', trigger: 'blur' },
					roomnumber: { required: true, message: '',  },
					roomimage: { required: true, message: '', trigger: 'blur' },
					roomarea: { required: true, message: '', trigger: 'blur' },
					roomwifi: { required: true, message: 'wifi' },
					roombed: { required: true, message: '' },
					roombreakfast: { required: true, message: '' },
					roomnum: { required: true, message: '' },
					roomprice: { required: true, message: ''},
				},
				//
				addForm: {
					roomintroduce:'',
					// roomnumber: '',
					roomimage:'',
					roomarea:'',
					roomwifi:1,
					roombed:'',
					roombreakfast:1,
					roomnum:'',
					roomprice:''
				}

			}
		},
		methods: {
			formatWifi: function (row, column) {
				return row.roomwifi == 1 ? '' : '';
			},
			formatBreakfast: function (row, column) {
				return row.roombreakfast == 1 ? '' : '';
			},
			handleCurrentChange(val) {
				this.currPage = val;
				this.getAllRooms();
			},
			//
			getAllRooms() {
				let para = {				
					currPage: this.currPage,
					pageSize: this.pageSize,
				}

				this.listLoading = true;
				//NProgress.start();
				getAllRoom(para).then((res) => {
					this.total = res.data.data.total;
					this.rooms = res.data.data.data;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { roomintroduce: row.roomintroduce };
					deleteRoom(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getAllRooms();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					roomintroduce:'',
					roomnumber: '',
					roomimage:'',
					roomarea:'',
					roomwifi:1,
					roombed:'',
					roombreakfast:1,
					roomnum:'',
					roomprice:''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.roomwifi = (para.roomwifi==1?'':'')
							para.roombreakfast = (para.roombreakfast==1?'':'')
							updateRoom(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getAllRooms();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.roomwifi = (para.roomwifi==1?'':'')
							para.roombreakfast = (para.roombreakfast==1?'':'')
							para.roomprice = parseFloat(para.roomprice)
							para.roomnum = String(para.roomnum)
							
							addRoom(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getAllRooms();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			// batchRemove: function () {
			// 	var ids = this.sels.map(item => item.id).toString();
			// 	this.$confirm('', '', {
			// 		type: 'warning'
			// 	}).then(() => {
			// 		this.listLoading = true;
			// 		//NProgress.start();
			// 		let para = { ids: ids };
			// 		batchRemoveUser(para).then((res) => {
			// 			this.listLoading = false;
			// 			//NProgress.done();
			// 			this.$message({
			// 				message: '',
			// 				type: 'success'
			// 			});
			// 			this.getAllRooms();
			// 		});
			// 	}).catch(() => {

			// 	});
			// }
		},
		mounted() {
			this.getAllRooms();
		}
	}

</script>

<style scoped>

</style>


