<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.uname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="findAdmin"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="admins"
      highlight-current-row
      v-loading="listLoading"
      @selection-change="selsChange"
      style="width: 100%;"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="uname" label="" width="250" sortable></el-table-column>
      <el-table-column prop="password" label="" width="250" sortable></el-table-column>

      <el-table-column label="" min-width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="20"
        :total="total"
        style="float:right;"
      ></el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="uname">
          <el-input v-model="editForm.uname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="editForm.password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="uname">
          <el-input v-model="addForm.uname" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="addForm.password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import { selAdmin, alterOrSaveAdmin, deleteAdmin, findAdmin } from "../../api/api";

export default {
  data() {
    return {
      filters: {
        name: ""
      },
      admins: [],
      total: 0,
      currPage: 1,
      pageSize: 20,
      listLoading: false,
      sels: [], //

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        uname: { required: true, message: "", trigger: "blur" },
        password: { required: true, message: "", trigger: "blur" }
      },
      //
      editForm: {
        uname: "",
        password: ""
      },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        uname: { required: true, message: "", trigger: "blur" },
        password: { required: true, message: "", trigger: "blur" }
      },
      //
      addForm: {
        uname: "",
        password: ""
      }
    };
  },
  methods: {
    handleCurrentChange(val) {
      this.currPage = val;
      this.getAdmins();
    },
    //
    getAdmins() {
      let para = {
        currPage: this.currPage,
        pageSize: this.pageSize
      };
      this.listLoading = true;
      //NProgress.start();
      selAdmin(para).then(res => {
        console.log("selAdmin==>", res);
        this.total = res.data.data.total;
        this.admins = res.data.data.data;
        this.listLoading = false;
        //NProgress.done();
      });
    },
    //
    findAdmin() {
      let para = {				
          uname: this.filters.uname,
          // currPage: this.currPage,
					// pageSize: this.pageSize,
				}
      this.listLoading = true;
      //NProgress.start();
      findAdmin(para).then(res => {
        // this.total = res.data.data.total;
        this.admins = res.data.data;
        this.listLoading = false;
        //NProgress.done();
      });
    },
    //
    handleDel: function(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { adminId: row.adminId };
          deleteAdmin(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getAdmins();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        uname: "",
        password: ""
      };
    },
    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.editForm);
            alterOrSaveAdmin(para).then(res => {
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getAdmins();
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.addForm);
            alterOrSaveAdmin(para).then(res => {
              this.addLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getAdmins();
            });
          });
        }
      });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    //
    // batchRemove: function() {
    //   var ids = this.sels.map(item => item.id).toString();
    //   this.$confirm("", "", {
    //     type: "warning"
    //   })
    //     .then(() => {
    //       this.listLoading = true;
    //       //NProgress.start();
    //       let para = { ids: ids };
    //       batchRemoveUser(para).then(res => {
    //         this.listLoading = false;
    //         //NProgress.done();
    //         this.$message({
    //           message: "",
    //           type: "success"
    //         });
    //         this.getAdmins();
    //       });
    //     })
    //     .catch(() => {});
    // }
  },
  mounted() {
    this.getAdmins();
  }
};
</script>

<style scoped>
</style>


