import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
// import Table from './views/nav1/Table.vue'
// import Form from './views/nav1/Form.vue'
// import user from './views/nav1/user.vue'
import room from './views/nav1/room.vue'
import coupon from './views/nav1/coupon.vue'
import article from './views/nav1/article.vue'
import popUpWin from './views/nav1/popUpWin.vue'
import goods from './views/nav1/goods.vue'
import banner from './views/nav1/banner.vue'

import admin from './views/nav2/admin.vue'
import company from './views/nav2/company.vue'
import member from './views/nav2/member.vue'

import getAllPayOrder from './views/nav3/getAllPayOrder.vue'
import getAllCompleteOrder from './views/nav3/getAllCompleteOrder.vue'
import roomOrder from './views/nav3/roomOrder.vue'



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            // { path: '/table', component: Table, name: 'Table' },
            // { path: '/form', component: Form, name: 'Form' },
            // { path: '/user', component: user, name: '' },
            { path: '/room', component: room, name: '' },
            { path: '/coupon', component: coupon, name: '' },
            { path: '/article', component: article, name: '' },
            { path: '/goods', component: goods, name: '' },
            { path: '/banner', component: banner, name: '' },
            // { path: '/popUpWin', component: popUpWin, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/company', component: company, name: '' },
            { path: '/member', component: member, name: '' },
            { path: '/admin', component: admin, name: '' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-address-card',
    //     leaf: true,//
    //     children: [
    //         { path: '/integralGoodOrder', component: integralGoodOrder, name: '' },
    //     ]
    // },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/getAllPayOrder', component: getAllPayOrder, name: '' },
            { path: '/getAllCompleteOrder', component: getAllCompleteOrder, name: '' },
            // { path: '/disposeOrder', component: disposeOrder, name: '' },
            // { path: '/getAllCompleteOrder', component: getAllCompleteOrder, name: '' },
            // { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


