<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.tiile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="getEssay"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="article"
      highlight-current-row
      v-loading="listLoading"
      @selection-change="selsChange"
      style="width: 100%;"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="60"></el-table-column>
      <!-- <el-table-column prop="aid" label="ID" width="120" sortable></el-table-column> -->
      <el-table-column prop="title" label="" width="200" sortable></el-table-column>
      <el-table-column prop="imageurl" label="" width="300" sortable></el-table-column>
      <el-table-column prop="createTime" label="" width="200" sortable></el-table-column>
      <el-table-column prop="content" label="" min-width="180" sortable></el-table-column>
      <el-table-column label="" width="150">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <el-pagination
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size="20"
        :total="total"
        style="float:right;"
      ></el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <!-- <el-form-item label="ID" prop="aid">
          <el-input v-model="editForm.aid" auto-complete="off"></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="title">
          <el-input v-model="editForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="imageurl">
          <el-input v-model="editForm.imageurl" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content"> 
          <el-input type="textarea" v-model="editForm.content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="title">
          <el-input v-model="addForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="imageurl">
          <el-input v-model="addForm.imageurl" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="addForm.content"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
import util from "../../common/js/util";
//import NProgress from 'nprogress'
import {
  selEssay,
  putEssay,
  alterEssay,
  deleteEssay
} from "../../api/api";

export default {
  data() {
    return {
      filters: {
        title: ""
      },
      article: [],
      total: 0,
      currPage: 1,
      pageSize: 20,
      listLoading: false,
      sels: [], //

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        title: { required: true, message: "", trigger: "blur" },
        imageurl: { required: true, message: "", trigger: "blur" },
        content: { required: true, message: "", trigger: "blur" },
      },
      //
      editForm: {
        aid:'',
        title:'',
        content:'',
        imageurl:''
      },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        title: { required: true, message: "", trigger: "blur" },
        imageurl: { required: true, message: "", trigger: "blur" },
        content: { required: true, message: "", trigger: "blur" },
      },
      //
      addForm: {
        title:'',
        content:'',
        imageurl:''
      }
    };
  },
  methods: {
    handleCurrentChange(val) {
      this.currPage = val;
      this.getEssay();
    },
    //
    getEssay() {
      let para = {
        currPage: this.currPage,
        pageSize: this.pageSize
      };
      this.listLoading = true;
      //NProgress.start();
      selEssay(para).then(res => {
        console.log("selEssay ==>", res);
        this.total = res.data.data.total;
        this.article = res.data.data.data;
        this.listLoading = false;
        //NProgress.done();
      });
    },
    //
    handleDel: function(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { aid: row.aid };
          deleteEssay(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getEssay();
          });
        })
        .catch(() => {});
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = Object.assign({}, row);
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        title:'',
        content:'',
        imageurl:'',
      };
    },
    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.editForm);
            alterEssay(para).then(res => {
              console.log('alterEssayparares==>',para,res)
              this.editLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["editForm"].resetFields();
              this.editFormVisible = false;
              this.getEssay();
            });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            //NProgress.start();
            let para = Object.assign({}, this.addForm);
            putEssay(para).then(res => {
              this.addLoading = false;
              //NProgress.done();
              this.$message({
                message: "",
                type: "success"
              });
              this.$refs["addForm"].resetFields();
              this.addFormVisible = false;
              this.getEssay();
            });
          });
        }
      });
    },
    selsChange: function(sels) {
      this.sels = sels;
    },
    // //
    // batchRemove: function() {
    //   var ids = this.sels.map(item => item.id).toString();
    //   this.$confirm("", "", {
    //     type: "warning"
    //   })
    //     .then(() => {
    //       this.listLoading = true;
    //       //NProgress.start();
    //       let para = { ids: ids };
    //       batchRemoveUser(para).then(res => {
    //         this.listLoading = false;
    //         //NProgress.done();
    //         this.$message({
    //           message: "",
    //           type: "success"
    //         });
    //         this.getEssay();
    //       });
    //     })
    //     .catch(() => {});
    // }
  },
  mounted() {
    this.getEssay();
  }
};
</script>

<style scoped>
</style>


